import React, { useState } from 'react'
import classes from './Paginator.module.css'


const Paginator = ({ portionSize = 10, totalUsersCount, pageSize, currentPage, onPageChanged, isFetching }) => {
    let pagesCount = Math.ceil(totalUsersCount / pageSize)

    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionPageNumber = portionNumber * portionSize

    if (isFetching) {
        return null
    } else {
        return (
            <div className={classes.container}>
                {portionNumber > 0 &&
                    <span className={classes.paginationN} onClick={() => { setPortionNumber(portionNumber - 1) }} ></span>
                }
                {pages
                    .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                    .map(p =>
                        <span className={currentPage === p ? classes.selectedPage : classes.paginationN}
                            key={p}
                            onClick={(e) => {
                                onPageChanged(p)
                            }}>{p}</span>
                    )}
                {portionCount > portionNumber &&
                    <span className={classes.paginationN} onClick={() => { setPortionNumber(portionNumber + 1) }} ></span>
                }
            </div>
        )
    }
}

export default Paginator
