import React from 'react'
import MyPostsContainer from "./MyPost/MyPostsContainer";
import classes from './Profile.module.css'
import Preloader from "../commen/Preloader/index";
import ProfileStatus from './ProfileStatus'
import usersPhoto from './../../assets/img/avatar.png'

const Profile = (props) => {
    if (!props.profile) {
        return <Preloader />
    }
    return (
        <div className={classes.wrapper}>
            <div className={classes.profile}>
                <div className={classes.userPic}>
                    <div className={classes.img}>
                        <img  src={props.profile.photos.small != null ? props.profile.photos.small : usersPhoto} alt="Avatar" />

                    </div>
                </div>
                <div className={`${classes.name} ${classes.border}`}>
                    <h3>John Doe</h3>
                    <ProfileStatus status={props.status} updateStatus={props.updateStatus}/>
                </div>
                <div className={`${classes.following} ${classes.border}`}>
                    <h4>Following</h4>
                    <span>34</span>
                </div>
                <div className={`${classes.followers} ${classes.border}`}>
                    <h4>Followers</h4>
                    <span>155</span>
                </div>
            </div>
            <MyPostsContainer  />

        </div>
    )
}


export default Profile