import React from 'react'
import classes from './Nav.module.css'
import {NavLink} from 'react-router-dom'

const Nav = () => {
    return (
        <div className={classes.nav}>
            <div className={classes.item}>
                <NavLink to="/profile" activeClassName="Nav_active__2W7gc">Profile</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to="/dialogs" activeClassName="Nav_active__2W7gc">Messages</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to="/users" activeClassName="Nav_active__2W7gc">Users</NavLink>
            </div>

        </div>
    )
}

export default Nav
