import React from 'react'
import Post from './Post'
import classes from './MyPosts.module.css'
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators";
import {Textarea} from "../../commen/FormsControls/index";

const maxLength10 = maxLengthCreator(10)

const MyPost = (props) => {

  let postsElements =
    props.posts.map( p => <Post text={p.text} id={p.id} key={p.id}/>)


  let addNewPost = (values) => {
      props.addPost(values.newPostBody)
  }

  return (
    <div className={classes.myPost}>
      <div>
        <AddPostFormRedux onSubmit={addNewPost} />
      </div>
      <div>
          {postsElements.reverse()}
      </div>
    </div>
  )
}

const AddPostForm = (props) => {
  return (
      <div className={classes.addFormPost}>
          <form onSubmit={props.handleSubmit}>
              <Field
                  component={Textarea}
                  name="newPostBody"
                  placeholder="Enter your post"
                  validate={[required, maxLength10 ]}
              />
              <button className={classes.btn}>Add post</button>
          </form>
      </div>
  )
}

const AddPostFormRedux = reduxForm({ form: 'newAddPostFormRedux'})(AddPostForm)

export default MyPost