import React from 'react'
import classes from './Dialogs.module.css'
import Dialog from './Dialog'
import Message from './Message'
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../commen/FormsControls/index";
import {required} from "../../utils/validators";

const Dialogs = (props) => {

    let state = props.dialogsPage

    let message = state.messages.map(m => <Message message={m.message} id={m.id} key={m.id}/>)

    let dialog = state.dialogs.map(d => <Dialog name={d.name} id={d.id} key={d.id}/>)

    let addNewMessage = (values) => {
        props.addMessage(values.newMessageBody)
    }

    return (
        <div className={classes.dialogs}>
            <div className={classes.dialogsWrapper} >
                <div>
                    <h3>Messages</h3>
                </div>
                <div >
                    {dialog}
                </div>
            </div>
            <div className={classes.messageWrapper}>
                <div>
                    <Dialog  name={"John Doe"}  />
                </div>
                {message}
                <AddMessageFormRedux onSubmit={addNewMessage} />
            </div>


        </div>
    )
}

const AddMessageForm = (props ) => {
    const { handleSubmit, pristine, reset, submitting } = props
    return (
        <div className={classes.addFormMessage}>
            <form onSubmit={props.handleSubmit} className={classes.form}>
                <Field
                    component={Textarea}
                    name="newMessageBody"
                    placeholder="Enter your message"
                    validate={[required]}
                />
                <button  className={classes.btn}>Add message</button>
            </form>
        </div>
    )
}

const AddMessageFormRedux = reduxForm({ form: "dialogAddMessageForm"})(AddMessageForm)

export default Dialogs
