import React from 'react'
import {Field, reduxForm} from "redux-form";
import {Input} from "../commen/FormsControls/index";
import {required} from "../../utils/validators";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";
import classesForm from './../commen/FormsControls/FromsControls.module.css'
import classes from './Login.module.css'

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div className={classes.formGrup} >
                <Field placeholder={"Email"} name={"email"} component={Input} validate={[required]} />
            </div>
            <div className={classes.formGrup} >
                <Field placeholder={"Password"} name={"password"} component={Input} validate={[required]} type={"password"}/>
            </div>
            <div className={classes.checkbox}>
                <Field type={"checkbox"} name={"rememberMe"} component={Input}/> Remember me
            </div>
            { props.error && <div className={classesForm.formSummaryError}>
                {props.error}
            </div> }
            <div>
                <button className={classes.btn}>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({ form: 'login'})(LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={"/profile"} />
    }

    return (
        <div className={classes.wrapperLogin}>
            <div className={classes.login}>
                <h1>Login</h1>
                <p>Sign into your account</p>
                <LoginReduxForm onSubmit={onSubmit}/>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login})(Login)