const ADD_MESSAGE = 'ADD-MESSAGE'

let initialState = {
  dialogs: [
    {  id: 1, name: 'John Doe'},
    {  id: 2, name: 'John'},
    {  id: 3, name: 'Doe'},
  ],
  messages: [
    {  id: 1, message: 'Lorem ipsum dolor sit ametc hendrerit...1'},
    {  id: 2, message: 'Aliquam luctus hendreri...2'},
    {  id: 3, message: 'Etiam id magna sit ametLorem ipsum dolor sit mett...3'},
  ],
}

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:
      let newMessage = action.newMessageBody
      return {
        ...state,
        messages: [ ...state.messages, { id:4, message: newMessage}],
      }
    default:
      return state
  }

}

export const addMessageAction = (newMessageBody) => ({ type: ADD_MESSAGE, newMessageBody })

export default dialogsReducer
