import React from 'react'
import usersPhoto from '../../assets/img/avatar.png'
import classes from './Users.module.css'
import {NavLink} from "react-router-dom"
import Paginator from "../commen/Paginator/Paginator";

const Users = ({totalUsersCount, pageSize, onPageChanged, currentPage, isFetching, ...props}) => {

    return (
      <>
        <div className={classes.users}>
            {
                props.users.map(u =>
                    <div className={classes.users__item} key={u.id}>
                        <NavLink to={`/profile/${u.id}`}>
                            <img src={u.photos.small != null ? u.photos.small : usersPhoto} alt="avatar"/>
                        </NavLink>
                        <span>{u.name}</span>
                        <p>{u.status != null  &&  u.status.length >= 20 ? u.status.slice(0, 20) + '...' : ' ' || u.status}</p>
                        {
                            u.followed
                                ? <button className={classes.btn} disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                                        props.unfollow(u.id)
                                    }
                                }>Unfollow</button>
                                : <button className={classes.btn} disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                                        props.follow(u.id)
                                    }
                                }>Follow</button>
                        }
                    </div>
                )
            }

        </div>
        <div className={classes.pagination}>
            <Paginator totalUsersCount={totalUsersCount} pageSize={pageSize}
                       onPageChanged={onPageChanged} currentPage={currentPage}  isFetching={isFetching}/>
        </div>
      </>
    )
}

export default Users
